  switch (submenu) {
    case "Call For Papers":
      router.push({ name: "cfp" });
      break;
    //case "Call For Late-Breaking Demo":
    //router.push({ name: "CallForLateBreakingDemo" });
    //break;
    case "Call For Tutorials":
