  switch (submenu) {
    case "HCMIR":
      router.push({ name: "HCMIR25Page" });
      break;
    case "DLfM":
      router.push({ name: "DLfM12thPage" });
      break;

    default:
      break;
  }
};

const handleItemClick = (item) => {
  router.push(item.route);
  drawer.value = false;
};
