<style lang="sass">
.menu-button {
  color: black;
  font-weight: bold;
  position: relative;
  padding: 0 16px;
}

.call-button {
  color: black !important;
  opacity: 1 !important;

  &.call-active::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 3px;
    background-color: black;
  }

  &:hover,
  &:active {
    color: grey;
  }
}

.logo-clickable {
  cursor: pointer;
}

@media (max-width: 600px) {
  .menu-button {
    font-size: 14px;
    line-height: 56px;
  }

  .call-button {
    font-size: 14px;
  }
}
</style>
